.article{
    padding-top: 30px;
}
.reviews-title {
    text-align: center;
    margin-bottom: 5px; 
    line-height: 1.2;
}

.carousel {
    height: 266px;
    @media screen and (max-width: 767.98px) {
        height: 200px;
    }
}

.article-contant__image {
    border-radius: 50%;
    width: 110px;
    cursor: pointer;
}

.article-contant__title {
    margin-bottom: 0;
    font-style: italic;
    font-weight: 500;
    font-size: 16px;
    padding: 16px 0;
    text-align: center; 
    color: var(--primary-text-color);
}

.article-contant__text {
    margin-bottom: 0;
    text-align: center;
    color: var(--primary-text-color);
    display: none;
}

.article-contant__text--active {
    display: block;
}

.reviews__stars {
    padding-top: 6px;
    width: 136px;
}

.reviews {
    opacity: 1;
    transition: opacity 0.3s ease-in-out;
}

.reviews--animation {
    opacity: 0;
}

.slick-slider {
    width: 290px;
    margin: 0 auto;

    @media screen and (min-width: 768px) {
        width: 626px;
    }
}

.slick-slide img {
    display: block;
    margin: 0 auto;
}

// This is needed to avoid bug with slick slider, re-initialization breaks animation
.slick-slide,
.slick-slide[aria-hidden="true"]:not(.slick-cloned) ~ .slick-cloned[aria-hidden="true"] {
    .article-contant__image {
        width: 110px;
        transition: all .22s linear;
    }
}

.slick-center,
.slick-slide[aria-hidden="true"]:not([tabindex="-1"]) + .slick-cloned[aria-hidden="true"] {
    .article-contant__image {
        @media screen and (min-width: 768px) {
            width: 185px;
        }

    }
}

.reviews__pagination-wrap{
    margin-top: 35px;
}
.reviews-pagination{
    margin-bottom: 0;
    text-align: center;
    position: relative;
    font-size: 14px;
    font-weight: 700;
    color: var(--accent-color);
    @media screen and (min-width: 1280px) {
        font-size: 16px;
    }
}
.reviews-pagination::after{

    @media screen and (min-width: 1280px){
        display: block;                  
        width:100px;                    
        content: '';                      
        position: absolute;              
        border-bottom: 1px solid var(--accent-color);   
        top:50%;                         
        right: 290px;  
    } 
 }

.reviews-pagination::before{

    @media screen and (min-width:1280px) {
        display: block;                  
        width:100px;                    
        content: '';                      
        position: absolute;              
        border-bottom: 1px solid var(--accent-color);   
        top:50%;                         
        left: 290px; 
    }
  
}